/* Global Styles */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
}

/* Header Styles */
.header {
    background-color: #4CAF50;
    color: white;
    text-align: center;
    padding: 20px;
}

.header h1 {
    margin: 0;
    font-size: 36px;
}

nav {
    margin-top: 10px;
}

nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
}

nav ul li {
    margin: 0 15px;
}

nav ul li a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    font-weight: bold;
}

nav ul li a:hover {
    color: yellow;
}

/* Body Styles */
.body-container {
    text-align: center;
    margin-top: 20px;
}

.crypto-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.card {
    width: 250px;
    padding: 15px;
    border: 2px solid black;
    text-align: center;
    background-color: #f9f9f9;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
    word-wrap: normal;
    border-radius: 10px;
    border-color: pink ;
    transition: transform 0.3s ease-in-out;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}

.card:hover {
    transform: scale(1.05);
}

.card img {
    width: 100px;
    border-radius: 50%;
}

.card h2 {
    font-size: 20px;
}

.card p {
    font-size: 18px;
    font-weight: bold;
}

.card a {
    text-decoration: none;
    color: blue;
    font-weight: bold;
}

/* Footer Styles */
.footer {
    text-align: center;
    background-color: #222;
    color: white;
    padding: 10px;
    margin-top: 20px;
}
